<template>
    <div>
        <v-row>
            <v-col cols="12" md="12" lg="12" sm="12"
                style="border-right: 1px solid lightgray"
            >
                <v-data-table
                    :headers="headers"
                    :items="items"
                    item-key="id"
                    :loading="loading"
                    disable-sort
                    hide-default-footer
                >
                    <!-- eslint-disable  -->
                    <template v-slot:item.name="{item, index}">
                        <v-menu 
                            v-model="menu[index]"
                            :close-on-content-click="false"
                            :internal-activator="true"
                            :nudge-width="220"
                            offset-x
                            offset-y>
                            <template v-slot:activator="{ on, attrs }">
                                <a class="d-flex" v-bind="attrs" v-on="on">
                                    <span style="color: #2782F6;">{{item.name}}</span>
                                </a>
                            </template>
                            <v-list class="process">
                                <div class="process-title">
                                    <span>Berilgan summa</span>
                                </div>
                                <div class="d-flex justify-space-between pa-1" v-for="(item2, index2) in item.payment_history" :key="index2">
                                   <span>Miqdor: {{formatPrice(item2.sum)}} so`m </span>
                                   <span>Sana: {{$dayjs(item2.created_at).format('DD-MM-YYYY')}}</span>
                                </div>

                                <div class="pa-1" v-if="item.payment_history.length == 0">
                                    0
                                </div>
                            </v-list>
                        </v-menu>
                    </template>

                    <template v-slot:item.total_debt="{item}">
                        <div class="d-flex justify-center">
                            <span class="text-black">{{ formatPrice(item.total_debt) }} so`m</span>
                        </div>
                    </template>

                    <template v-slot:item.total_paid="{item}">
                        <div class="d-flex justify-center">
                            <span class="text-black">{{ formatPrice(item.total_paid) }} so`m</span>
                        </div>
                    </template>

                    <template v-slot:item.created_at="{item}">
                        <div class="d-flex justify-center">
                            <span class="text-black">{{ $dayjs(item.created_at).format('DD-MM-YYYY') }}</span>
                        </div>
                    </template>

                    <template v-slot:item.actions="{item}">
                        <div class="d-flex align-center justify-center">
                            <div class="cursor-pointer mr-2 mt-1" v-if="$can('read', 'finance_kirim_money')"
                            @click="(paymentModal = true) && (element = item)">
                                <img src="@/assets/icons/add-square-green.svg"
                                 alt="add-square-green" class="cursor-pointer">
                            </div>
                            <!-- <div class="cursor-pointer mr-2 mt-1" v-if="$can('update', 'finance_kirim')">
                                <img src="@/assets/icons/edit.svg" alt="edit" class="cursor-pointer">
                            </div>
                            <div class="cursor-pointer mt-1" v-if="$can('delete', 'finance_kirim')">
                                <img src="@/assets/icons/trush-square.svg" alt="trush-square">
                            </div> -->
                        </div>
                    </template>


                    <template v-slot:no-data>
                        <div>
                            <no-data class="mt-6"></no-data>
                            <br>
                            <h2 class="ml-6 mt-1">Ma'lumotlar topilmadi!</h2>
                        </div>
                    </template>

                </v-data-table>
            </v-col>
        </v-row>
        <v-row>
            <v-col>
                <v-pagination
                    v-model="page"
                    :length="pageCount"
                    total-visible="7"
                    class="my-2"
                ></v-pagination>
            </v-col>
        </v-row>

        <ModalIncomePayment :paymentModal="paymentModal" :item="element"></ModalIncomePayment>
    </div>
</template>

<script>
import {mapActions, mapState} from "vuex";
import {priceMixin} from '@/mixins/formatter.js';
import NoData from "@/components/base/NoData.vue";
import ModalIncomePayment from '@/components/modals/Finance/ModalIncomePayment'

export default {
    mixins: [priceMixin],
    components: {
        ModalIncomePayment,
        NoData,
    },
    data() {
        return {
            menu: [],
            page: 1,
            pageCount: 0,
            paymentModal: false,
            loading: false,
            items: [],
            element: {},
            headers: [
                {text: 'Nomi', value: 'name', align: 'start', class: 'table-header'},
                {text: 'Umumiy qarzdorlik', value: 'total_debt', align: 'center', class: 'table-header'},
                {text: 'Umumiy berildi', value: 'total_paid', align: 'center', class: 'table-header'},
                {text: 'Sana', value: 'created_at', align: 'center', class: 'table-header'},
                {text: 'Harakatlar', value: 'actions', align: 'center', class: 'table-header'},
            ],
        }
    },
    watch: {
        // ============= OTHER_WATCHERS START =============
        page: function (newVal, oldVal) {
            if (newVal) {
                this.refresh()
            }
        },
        '$store.state.main.finance_income': {
            handler: async function(value) {
                if(value){
                    await this.refresh()
                    this.$store.commit('main/SET_UPDATE_FINANCE_INCOME', false)
                }
            },
            deep: true,
            immediate: true
        },
        // ============= OTHER_WATCHERS END =============
    },
    async mounted() {
        await this.refresh();
        if(!this.$can('read', 'finance_kirim_money')){
            this.headers.pop();
        }
    },
    computed: {
        ...mapState(
            { 
                debtors: state => state.finance.debtors,
                searchValue: state => state.main.searchValue,
                dateFilterValue: state => state.main.dateFilterValue
            }
        ),
    },
    methods: {
        ...mapActions(
            {fetchDebtors: 'finance/fetchDebtors'}
        ),
        async refresh() {
            this.loading = true
            await this.fetchDebtors({page: this.page, search: this.searchValue, date: this.dateFilterValue});

            this.page = this.debtors.current_page;
            this.pageCount = Math.ceil(this.debtors.total / 20);
            this.items = this.debtors.data;
            this.loading = false

            this.items.forEach(element => {
                element.name = element.client.name
            });
           
        },
    }
};
</script>

