<template>
    <div>
        <v-row>
            <v-col cols="12" md="6" lg="6" sm="12" style="border-right: 1px solid lightgray">
                <v-data-table
                    :headers="headers"
                    :items="items"
                    item-key="id"
                    :loading="loading"
                    disable-sort
                    hide-default-footer
                >
                    <!-- eslint-disable  -->
                    <template v-slot:item.where="{item, index}">
                        <v-menu 
                            v-model="menu[index]"
                            :close-on-content-click="false"
                            :nudge-width="220"
                            offset-x
                            offset-y>
                            <template v-slot:activator="{ on, attrs }">
                                <a class="d-flex" v-bind="attrs" v-on="on">
                                    <span style="color: #2782F6;">{{item.where}}</span>
                                </a>
                            </template>
                            <v-list class="process">
                                <div class="process-title">
                                    <span>Qo'shimcha ma'lumotlar</span>
                                </div>

                                <div class="pa-1" v-if="outcomeType == 'all_expenses'">-</div>
                                <div class="pa-1" v-if="outcomeType == 'expense_pay'">-</div>
                                
                                <div class="pa-1" v-if="outcomeType == 'outgoing_pay'">Mahsulot nomi: {{item.order.product.name}}</div>
                                <div class="pa-1" v-if="outcomeType == 'outgoing_pay'">Soni: {{formatPrice(item.order.amount)}} ta</div>
                                <div class="pa-1" v-if="outcomeType == 'outgoing_pay'">Dona narxi: {{formatPrice(item.order.price)}} so`m</div>
                                <div class="pa-1" v-if="outcomeType == 'outgoing_pay'">Umumiy narxi: {{formatPrice(item.order.total_price)}} so`m</div>

                                <div class="pa-1" v-if="outcomeType == 'product_income_pay'">Qaerdan: {{item.product_income.from_where}}</div>
                                <div class="pa-1" v-if="outcomeType == 'product_income_pay'">Mahsulot nomi: {{item.product_income.product.name}}</div>
                                <div class="pa-1" v-if="outcomeType == 'product_income_pay'">Mahsulot kodi: #{{item.product_income.product.code}}</div>
                                <div class="pa-1" v-if="outcomeType == 'product_income_pay'">Soni: {{formatPrice(item.product_income.amount)}} ta</div>
                                <div class="pa-1" v-if="outcomeType == 'product_income_pay'">Dona narxi: {{formatPrice(item.product_income.price)}} so`m</div>
                                <div class="pa-1" v-if="outcomeType == 'product_income_pay'">Umumiy narxi: {{formatPrice(item.product_income.total_price)}} so`m</div>


                                <div class="pa-1" v-if="outcomeType == 'sewing_pay'">Mahsulot nomi: {{item.sewing_report.product.name}}</div>
                                <div class="pa-1" v-if="outcomeType == 'sewing_pay'">Mahsulot kodi: #{{item.sewing_report.product.code}}</div>
                                <div class="pa-1" v-if="outcomeType == 'sewing_pay'">Mahsulot rangi: {{item.sewing_report.color}}</div>
                                <div class="pa-1" v-if="outcomeType == 'sewing_pay'">Soni: {{formatPrice(item.sewing_report.amount)}} ta</div>
                                <div class="pa-1" v-if="outcomeType == 'sewing_pay'">Umumiy narxi: {{formatPrice(item.sewing_report.total_price)}} so`m</div>

                            </v-list>
                        </v-menu>
                    </template>

                    <template v-slot:item.sum="{item}">
                        <div class="d-flex justify-center">
                            <span class="text-black">{{ formatPrice(item.sum) }} so`m</span>
                        </div>
                    </template>

                    <template v-slot:item.date="{item}">
                        <div class="d-flex justify-center">
                            <span class="text-black">{{ item.date }}</span>
                        </div>
                    </template>

                    <!-- <template v-slot:item.actions="{item}">
                        <div class="d-flex align-center justify-center">
                            <div class="cursor-pointer mr-2 mt-1" v-if="$can('update', 'finance_chiqim')">
                                <img src="@/assets/icons/edit.svg" alt="edit" class="cursor-pointer">
                            </div>
                            <div class="cursor-pointer mt-1" v-if="$can('delete', 'finance_chiqim')">
                                <img src="@/assets/icons/trush-square.svg" alt="edit-2">
                            </div>
                        </div>
                    </template> -->

                    <template v-slot:no-data>
                        <div>
                            <no-data class="mt-6"></no-data>
                            <br>
                            <h2 class="ml-6 mt-1">Ma'lumotlar topilmadi!</h2>
                        </div>
                    </template>

                </v-data-table>
            </v-col>
            <v-col cols="12" md="6" lg="6" sm="12">
                <v-data-table
                    :headers="headers"
                    :items="items2"
                    item-key="id"
                    disable-sort
                    hide-default-footer
                >
                    <!-- eslint-disable  -->
                    <template v-slot:item.where="{item, index}">
                        <v-menu 
                            v-model="menu2[index]"
                            :close-on-content-click="false"
                            :nudge-width="220"
                            offset-x
                            offset-y>
                            <template v-slot:activator="{ on, attrs }">
                                <a class="d-flex" v-bind="attrs" v-on="on">
                                    <span style="color: #2782F6;">{{item.where}}</span>
                                </a>
                            </template>
                            <v-list class="process">
                                <div class="process-title">
                                    <span>Qo'shimcha ma'lumotlar</span>
                                </div>

                                <div class="pa-1" v-if="outcomeType == 'all_expenses'">-</div>
                                <div class="pa-1" v-if="outcomeType == 'expense_pay'">-</div>
                                
                                <div class="pa-1" v-if="outcomeType == 'outgoing_pay'">Mahsulot nomi: {{item.order.product.name}}</div>
                                <div class="pa-1" v-if="outcomeType == 'outgoing_pay'">Soni: {{formatPrice(item.order.amount)}} ta</div>
                                <div class="pa-1" v-if="outcomeType == 'outgoing_pay'">Dona narxi: {{formatPrice(item.order.price)}} so`m</div>
                                <div class="pa-1" v-if="outcomeType == 'outgoing_pay'">Umumiy narxi: {{formatPrice(item.order.total_price)}} so`m</div>

                                <div class="pa-1" v-if="outcomeType == 'product_income_pay'">Qaerdan: {{item.product_income.from_where}}</div>
                                <div class="pa-1" v-if="outcomeType == 'product_income_pay'">Mahsulot nomi: {{item.product_income.product.name}}</div>
                                <div class="pa-1" v-if="outcomeType == 'product_income_pay'">Mahsulot kodi: #{{item.product_income.product.code}}</div>
                                <div class="pa-1" v-if="outcomeType == 'product_income_pay'">Soni: {{formatPrice(item.product_income.amount)}} ta</div>
                                <div class="pa-1" v-if="outcomeType == 'product_income_pay'">Dona narxi: {{formatPrice(item.product_income.price)}} so`m</div>
                                <div class="pa-1" v-if="outcomeType == 'product_income_pay'">Umumiy narxi: {{formatPrice(item.product_income.total_price)}} so`m</div>


                                <div class="pa-1" v-if="outcomeType == 'sewing_pay'">Mahsulot nomi: {{item.sewing_report.product.name}}</div>
                                <div class="pa-1" v-if="outcomeType == 'sewing_pay'">Mahsulot kodi: #{{item.sewing_report.product.code}}</div>
                                <div class="pa-1" v-if="outcomeType == 'sewing_pay'">Mahsulot rangi: {{item.sewing_report.color}}</div>
                                <div class="pa-1" v-if="outcomeType == 'sewing_pay'">Soni: {{formatPrice(item.sewing_report.amount)}} ta</div>
                                <div class="pa-1" v-if="outcomeType == 'sewing_pay'">Umumiy narxi: {{formatPrice(item.sewing_report.total_price)}} so`m</div>

                            </v-list>
                        </v-menu>
                    </template>

                    <template v-slot:item.sum="{item}">
                        <div class="d-flex justify-center">
                            <span class="text-black">{{ formatPrice(item.sum) }} so`m</span>
                        </div>
                    </template>

                    <template v-slot:item.date="{item}">
                        <div class="d-flex justify-center">
                            <span class="text-black">{{ item.date }}</span>
                        </div>
                    </template>

                    <!-- <template v-slot:item.actions="{item}">
                        <div class="d-flex align-center justify-center">
                            <div class="cursor-pointer mr-2 mt-1" v-if="$can('update', 'finance_chiqim')">
                                <img src="@/assets/icons/edit.svg" alt="edit" class="cursor-pointer">
                            </div>
                            <div class="cursor-pointer mt-1" v-if="$can('delete', 'finance_chiqim')">
                                <img src="@/assets/icons/trush-square.svg" alt="edit-2">
                            </div>
                        </div>
                    </template> -->

                    <template v-slot:no-data>
                        <div>
                            <no-data class="mt-6"></no-data>
                            <br>
                            <h2 class="ml-6 mt-1">Ma'lumotlar topilmadi!</h2>
                        </div>
                    </template>

                </v-data-table>
            </v-col>
        </v-row>
        <v-row>
            <v-col>
                <v-pagination
                    v-model="page"
                    :length="pageCount"
                    total-visible="7"
                    class="my-2"
                ></v-pagination>
            </v-col>
        </v-row>
      
    </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
import { priceMixin } from '@/mixins/formatter.js'
import NoData from "@/components/base/NoData.vue";
// import { loaderMixin } from '@/mixins/dateLoaderMixins.js'
// import DeleteModal from '@/components/modals/DeleteModal.vue'

export default {
    mixins: [priceMixin],
    components: {
        // DeleteModal,
        NoData
    },
    data() {
        return {
            menu: [],
            menu2: [],
            page: 1,
            pageCount: 0,
            editModal: false,
            deleteModal: false,
            loading: false,
            headers: [
                {text: 'Qayerga', value: 'where', align: 'start', class: 'table-header'},
                {text: 'Summasi', value: 'sum', align: 'center', class: 'table-header'},
                {text: 'Sana', value: 'date', align: 'center', class: 'table-header'},
                // {text: 'Harakatlar', value: 'actions', align: 'center', class: 'table-header'},
            ],
            items: [],
            items2: [],
        }
    },
    async mounted(){
        await this.refreshAllExpenses()
    },
    watch:{
        page: async function (newVal, oldVal) {
            if (newVal) {
                if (this.outcomeType == 'all_expenses') {
                    await this.refreshAllExpenses()
                }else if(this.outcomeType == 'outgoing_pay') {
                    await this.refreshOutgoingPay()
                }else if(this.outcomeType == 'product_income_pay') {
                    await this.refreshProductIncomePay()
                }else if(this.outcomeType == 'sewing_pay') {
                    await this.refreshSewingPay()
                }else if(this.outcomeType == 'expense_pay') {
                    await this.refreshExpansePay()
                }
            }
        },
        '$store.state.main.fctory_closed_orders': {
            handler: async function(value) {
                if(value){
                    await this.refresh()
                    this.$store.commit('main/SET_UPDATE_FACTORY_CLOSED_ORDERS', false)
                }
            },
            deep: true,
            immediate: true
        },
        outcomeType: async function (newVal, oldVal) {
            if (newVal == 'all_expenses') {
                await this.refreshAllExpenses();
            }else if(newVal == 'outgoing_pay') {
                await this.refreshOutgoingPay();
            }else if(newVal == 'product_income_pay') {
                await this.refreshProductIncomePay();
            }else if(newVal == 'sewing_pay') {
                await this.refreshSewingPay();
            }else if(newVal == 'expense_pay') {
                await this.refreshExpansePay();
            }
        },

    },
    computed: {
        ...mapState({
            outcomeType: state => state.finance.outcomeType,
            factory_sewing_history: state => state.factory.factory_sewing_history,
            all_expenses: state => state.finance.all_expenses,
            orderPayments: state => state.finance.orderPayments,
            productIncomePayments: state => state.finance.productIncomePayments,
            sewingPayments: state => state.finance.sewingPayments,
            expenses: state => state.finance.expenses,
        })
    },
    methods: {
        ...mapActions({
            fetchFactorySewingHistory: 'factory/fetchFactorySewingHistory',
            fetchExpenses: 'finance/fetchExpenses',
            fetchAllOrderPayments: 'finance/fetchAllOrderPayments',
            fetchAllProductIncomePayments: 'finance/fetchAllProductIncomePayments',
            fetchAllSewingPayments: 'finance/fetchAllSewingPayments',
            fetchExpansePayments: 'finance/fetchExpansePayments',
        }),
        async refreshAllExpenses(){
            this.loading = true

            await this.fetchExpenses({page: this.page});
            this.page = this.all_expenses.current_page;
            this.pageCount = Math.ceil(this.all_expenses.total / 20);
            this.items2 = this.all_expenses.data.slice(10, this.all_expenses.data.length);
            this.items = this.all_expenses.data;

            this.items.forEach(elem => {
                elem.where = elem.expense_name
                elem.date = this.$dayjs(elem.created_at).format('DD-MM-YYYY') 
            })
            this.items2.forEach(elem => {
                elem.where = elem.expense_name
                elem.date = this.$dayjs(elem.created_at).format('DD-MM-YYYY') 
            })
            this.loading = false
        },
        async refreshOutgoingPay(){
            this.loading = true
            await this.fetchAllOrderPayments({page: this.page});
            this.page = this.orderPayments.current_page;
            this.pageCount = Math.ceil(this.orderPayments.total / 20);
            this.items2 = this.orderPayments.data.slice(10, this.orderPayments.data.length);
            this.items = this.orderPayments.data;

            this.items.forEach(elem => {
                elem.where = elem.order.factory.name
                elem.date = this.$dayjs(elem.created_at).format('DD-MM-YYYY') 
            })
            this.items2.forEach(elem => {
                elem.where = elem.order.factory.name
                elem.date = this.$dayjs(elem.created_at).format('DD-MM-YYYY') 
            })
            this.loading = false
        },
        async refreshProductIncomePay(){
            this.loading = true
            await this.fetchAllProductIncomePayments({page: this.page});
            this.page = this.productIncomePayments.current_page;
            this.pageCount = Math.ceil(this.productIncomePayments.total / 20);
            this.items2 = this.productIncomePayments.data.slice(10, this.productIncomePayments.data.length);
            this.items = this.productIncomePayments.data;

            this.items.forEach(elem => {
                elem.where = elem.product_income.product.name
                elem.date = this.$dayjs(elem.created_at).format('DD-MM-YYYY') 
            })
            this.items2.forEach(elem => {
                elem.where = elem.product_income.product.name
                elem.date = this.$dayjs(elem.created_at).format('DD-MM-YYYY') 
            })
            this.loading = false
        },
        async refreshSewingPay(){
            this.loading = true
            await this.fetchAllSewingPayments({page: this.page});
            this.page = this.sewingPayments.current_page;
            this.pageCount = Math.ceil(this.sewingPayments.total / 20);
            this.items2 = this.sewingPayments.data.slice(10, this.sewingPayments.data.length);
            this.items = this.sewingPayments.data;

            this.items.forEach(elem => {
                elem.where = elem.sewing_report.product.name
                elem.date = this.$dayjs(elem.created_at).format('DD-MM-YYYY') 
            })
            this.items2.forEach(elem => {
                elem.where = elem.sewing_report.product.name
                elem.date = this.$dayjs(elem.created_at).format('DD-MM-YYYY') 
            })
            this.loading = false
        },
        async refreshExpansePay(){
            this.loading = true
            await this.fetchExpansePayments({page: this.page});
            this.page = this.expenses.current_page;
            this.pageCount = Math.ceil(this.expenses.total / 20);
            this.items2 = this.expenses.data.slice(10, this.expenses.data.length);
            this.items = this.expenses.data;

            this.items.forEach(elem => {
                elem.where = elem.description
                elem.date = this.$dayjs(elem.created_at).format('DD-MM-YYYY') 
            })
            this.items2.forEach(elem => {
                elem.where = elem.description
                elem.date = this.$dayjs(elem.created_at).format('DD-MM-YYYY') 
            })
            this.loading = false
        },
        closeEditModal(){
            this.editModal = false
        }
    }
};
</script>

