                <v-card class="cardsMain">
                    <span class="text-muted">Umumiy qarzdorlik</span>
                    <span class="mass">{{ formatPrice(analyticData.debt) }} so'm</span>
                    <span></span>
                </v-card>
                <v-card class="cardsMain">
                    <span class="text-muted">Mato to’quv</span>
                    <span class="mass">{{ formatPrice(analyticData.materialDebtors)  }} so'm</span>
                    <span></span>
                </v-card>
                <v-card class="cardsMain">
                    <span class="text-muted">Yoqa to’quv</span>
                    <span class="mass">{{ formatPrice(analyticData.collarDebtors)  }} so'm</span>
                    <span></span>
