            <v-tab-item :value="'tab-2'">
                <Collar v-if="$can('read', 'finance_qarzdorlik_yoqa_toquv')"></Collar>
            </v-tab-item>

            <v-tab-item :value="'tab-3'">
                <Painting v-if="$can('read', 'finance_qarzdorlik_boyoqxona')"></Painting>
            </v-tab-item>

            <v-tab-item :value="'tab-4'">
                <Factories v-if="$can('read', 'finance_qarzdorlik_fabrikalar')"></Factories>
            </v-tab-item>

