<script>
import {mapMutations} from "vuex";
import { priceMixin } from '@/mixins/formatter.js';
import Material from './Material.vue'
import Collar from './Collar.vue'
import Painting from './Painting.vue'
import Factories from './Factories.vue'
import Tailoring from './Tailoring.vue'

export default {
    components: {
        Material,
        Collar,
        Painting,
        Factories,
        Tailoring
    },
    mixins: [priceMixin],
    data() {
        return {
            analyticData: [],
            tab: 'tab-1',
            debtsTab: 'tab-1',
        }
    },
    mounted() {
        this.$axios.get("/finance-analytics").then(response => {
            this.analyticData = response.data;
            console.log(response.data)
        });
    },
    computed: {
        routeNAME: {
            get: function() {
                return this.$store.state.main.routeNAME
            },
            set: function(newValue) {
                return this.$store.state.main.routeNAME = newValue
            }
        },
    },
    methods: {
        ...mapMutations(
            {

            }),
    }
};
</script>

