<template>
    <div>
        <v-menu offset-y v-model="filterMenu" :close-on-content-click="false">
            <template v-slot:activator="{  }">
                <v-btn elevation="0" @click="filterMenu = !filterMenu">
                    <img src="@/assets/images/filter.svg" alt="filter">
                </v-btn>
            </template>
    
            <v-list class="filter-menu">
                <div class="px-4">
                  
                    <v-radio-group v-model="chooseFilter">
                        <div v-for="(item, index) in finance_outcome" :key="index" class="d-flex align-center justify-space-between">
                            <span>{{item.name}}</span>
                            <v-radio
                                class="py-2"
                                :value="item.value"
                            ></v-radio>
                        </div>
                    </v-radio-group>

                    <div class="border-top mb-6"></div>
                   
                    <v-btn elevation="0" class="primaryBtn mb-3" @click="filterExpenses()">
                        Ko'rsatish
                    </v-btn>
                </div>
            </v-list>
        </v-menu>
    </div>
</template>

<script>
import {mapActions, mapState} from "vuex";
import {priceMixin} from '@/mixins/formatter.js';
import {loaderMixin} from '@/mixins/dateLoaderMixins.js'

export default {
    mixins: [priceMixin, loaderMixin],
    props: ['filterMenu', 'permission'],
    components: {
        
    },
    data() {
        return {
            finance_outcome: [
                { name: 'Barcha xarajatlar', value: 'all_expenses'}, 
                { name: 'Buyurtma xarajatlari', value: 'outgoing_pay'}, 
                { name: 'Mahsulot xarajatlari', value: 'product_income_pay'}, 
                { name: 'Bichuv xarajatlari', value: 'sewing_pay'}, 
                { name: 'Boshqa xarajatlar', value: 'expense_pay'}, 
            ],
            chooseFilter: 'all_expenses',
            // page: 1,
            // pageCount: 0,
            // deleteModal: false,
            // incomePaymentsSecond: [],
        }
    },
    watch: {
        page: function (newVal, oldVal) {
            if (newVal) {
                this.refresh()
            }
        },
        
    },
    async mounted() {
        await this.refresh();
    },
    computed: {
        ...mapState(
            { 
                // incomePaymentFields: state => state.finance.incomePayments 
            }
        ),
        outcomeType: {
            get: function() {
                return this.$store.state.finance.outcomeType
            },
            set: function(newValue) {
                return this.$store.state.finance.outcomeType = newValue
            }
        },
    },
    methods: {
        ...mapActions(
            {
                // getAllExpenses
                // fetchIncomePayments: 'finance/fetchAllIncomePayments'
            }
        ),
        async refresh() {
            this.loading = true
            // await this.fetchIncomePayments({page: this.page});

            // this.page = this.incomePaymentFields.current_page;
            // this.pageCount = Math.ceil(this.incomePaymentFields.total / 20);
            // this.incomePaymentsSecond = this.incomePaymentFields.data.slice(10, this.incomePaymentFields.data.length);
            // this.incomePayments = this.incomePaymentFields.data;

            this.loading = false
        },
        async filterExpenses(){
            if (this.chooseFilter == 'all_expenses') {
                this.outcomeType = 'all_expenses'
            }else if (this.chooseFilter == 'outgoing_pay') {
                this.outcomeType = 'outgoing_pay'
            }else if (this.chooseFilter == 'product_income_pay') {
                this.outcomeType = 'product_income_pay'
            }else if (this.chooseFilter == 'sewing_pay') {
                this.outcomeType = 'sewing_pay'
            }else if (this.chooseFilter == 'expense_pay') {
                this.outcomeType = 'expense_pay'
            }

            this.$parent.filterMenu = false;
            this.$parent.tab = 'tab-2'
        }
    }
};
</script>

<style lang="scss">
.filter-menu{
    min-width: 275px;

    .primaryBtn{
        width: 100%;
    }
    
    // .check .v-input .v-input__control input{
    //     margin-top: 40px!important;
    // }
}
</style>