<template>
    <div>
        <v-dialog
            max-width="400"
            persistent
            v-model="exitDialog"
        >
            <template v-slot:activator="{ on, attrs }">
                <v-btn dark v-bind="attrs" class="mr-2"
                       v-on="on" elevation="0" style="background:#EEA36C; text-transform: capitalize;">
                    <img src="@/assets/images/wallet-minus.svg" alt="wallet-minus" class="mr-1" width="22">
                    <span class="fs-16px">Chiqim</span>
                </v-btn>
            </template>
            <template>
                <v-card class="aksessuar_add">

                    <div class="aksessuar_add__title">
                        <span class="text-black">Chiqim bo`lmoqda</span>
                        <v-btn icon @click="closeDialog">
                            <img src="@/assets/images/BackButton.svg" alt="BackButton">
                        </v-btn>
                    </div>

                    <ValidationObserver ref="formExit" v-slot="{ handleSubmit, invalid }">
                        <form class="aksessuar_add__content" @submit.prevent="handleSubmit(onSubmit)">

                            <div class="mt-3">
                                <span class="text-black ml-1">Sana</span>
                                <v-menu
                                    v-model="dateMenu"
                                    :close-on-content-click="false"
                                    :nudge-right="40"
                                    transition="scale-transition"
                                    offset-y
                                    min-width="auto"
                                >
                                    <template v-slot:activator="{ on, attrs }">
                                        <ValidationProvider rules="required" v-slot="{ errors }">
                                            <v-text-field
                                                class="mt-2"
                                                placeholder="Tanlang"
                                                v-model="dateFormatted"
                                                readonly
                                                outlined
                                                v-bind="attrs"
                                                v-on="on"
                                                @blur="date = parseDate(dateFormatted)"
                                                :error-messages="errors"
                                            ></v-text-field>
                                        </ValidationProvider>
                                    </template>
                                    <ValidationProvider rules="required" v-slot="{ errors }">
                                        <v-date-picker
                                            color="blue lighten-1"
                                            v-model="date"
                                            @input="dateMenu = false"
                                            locale="ru-ru"
                                            first-day-of-week="1"
                                            :error-messages="errors"
                                        ></v-date-picker>
                                    </ValidationProvider>
                                </v-menu>
                            </div>

                            <div>
                                <span class="text-black ml-1">Xarajat nomi</span>
                                <ValidationProvider :rules="{required: true,
                                error: [errorStatus, errorMessage, errorPlace, 'from_where']}"
                                v-slot="{ errors }">
                                    <v-text-field outlined class="mt-2" :error-messages="errors" maxlength="100"
                                        placeholder="Misol: Arenda uchun" v-model="expense.description">
                                    </v-text-field>
                                </ValidationProvider>
                            </div>

                            <div>
                                <span class="text-black ml-1">Xarajat summa</span>
                                <ValidationProvider :rules="{required: true,
                                error: [errorStatus, errorMessage, errorPlace, 'total_price']}"
                                    v-slot="{ errors }">
                                    <v-text-field
                                        outlined :error-messages="errors"
                                        onkeypress="return /[0-9,/.]/i.test(event.key)"
                                        @input="formatPrice(expense.sum, 'total_price')"
                                        v-model="expense.sum" class="mt-2"
                                        placeholder="Misol: 1,240,000 so’m">
                                    </v-text-field>
                                </ValidationProvider>
                            </div>

                            <v-card-actions class="d-flex justify-space-between">
                                <v-btn
                                    text
                                    class="backBtn"
                                    elevation="0"
                                    @click="closeDialog"
                                >Bekor qilish
                                </v-btn>
                                <v-btn
                                    class="primaryBtn"
                                    elevation="0"
                                    :loading="loading"
                                    :disabled="invalid"
                                    type="submit"
                                    @click="loader = 'loading'"
                                >Tasdiqlash
                                </v-btn>
                            </v-card-actions>

                        </form>
                    </ValidationObserver>
                </v-card>
            </template>
        </v-dialog>
    </div>
</template>

<script>
import {mapActions, mapState} from "vuex";
import {dateMixin, loaderMixin} from '@/mixins/dateLoaderMixins.js'

export default {
    props: [''],
    mixins: [dateMixin, loaderMixin],
    components: {},
    data() {
        return {
            // ============= SECONDARY_DATA =============
            exitDialog: false,
            // ============= SECONDARY_DATA =============


            // ============= PRIMARY_DATA =============
            expense: {
                sum: 0,
                description: '',
                created_at: this.date
            },
            errorStatus: false,
            errorMessage: '',
            errorPlace: '',
            // ============= PRIMARY_DATA =============
        }
    },
    async mounted() {
        // await this.refresh()
    },
    watch: {
        // ============= OTHER_WATCHERS START =============
        'expense': {
            handler(value){
                if(value){
                    this.clearErrors()
                }   
            }, deep: true,
        },
        // ============= OTHER_WATCHERS END =============
    },
    computed: {
        ...mapState({
            productCodesFields: state => state.finance.expense,
        }),

    },
    methods: {
        ...mapActions(
            {
                postNewExpense: 'finance/postAddExpenses'
            }
        ),


        // ============= REFRESH START =============
        async refresh() {

        },
        // ============= REFRESH END =============






        // ============= PRICE_FORMATTER START =============
        formatPrice(value, input) {
            if(value != ''){

                let currency = value.toString().replace(/,/g, '');
                currency = parseFloat(currency)
                currency = new Intl.NumberFormat('en-US', {minimumFractionDigits: 0, maximumFractionDigits: 2}).format(currency);


                if(input == 'total_price'){
                    this.expense.sum = currency
                }
            }
        },
        // ============= PRICE_FORMATTER END =============






        // ============= CLEARS_AND_CLOSES START =============
        clearInputs() {
            this.date = ''
            this.expense.sum = 0,
            this.expense.description = '',
            this.clearErrors()
        },
        clearErrors() {
            this.errorStatus = false;
            this.errorMessage = '';
            this.errorPlace = '';
        },
        closeDialog() {
            this.clearInputs();
            this.exitDialog = false
            this.$refs.formExit.reset();
        },
        // ============= CLEARS_AND_CLOSES END =============







        // ============= ON_SUBMIT3 START =============
        async onSubmit() {
            this.$refs.formExit.validate().then(async (success) => {
                if (success) {
                    // ============= IF SUCCESS =============


                    // ============= ADDING_NEW_IP START =============
                    let aksessuar = await this.postNewExpense(
                        {
                            created_at: this.date,
                            sum: parseFloat(this.expense.sum.toString().replace(/,/g, '')),
                            description: this.expense.description
                        })

                    if (aksessuar.response && aksessuar.response.status == 422) {
                        if (aksessuar.response.data.errors.sum) {
                            this.errorMessage = aksessuar.response.data.errors.sum[0];
                            this.errorPlace = 'sum'
                        } else if (aksessuar.response.data.errors.description) {
                            this.errorMessage = aksessuar.response.data.errors.description[0];
                            this.errorPlace = 'description'
                        } else if (aksessuar.response.data.errors.created_at) {
                            this.errorMessage = aksessuar.response.data.errors.created_at[0];
                            this.errorPlace = 'created_at'
                        }
                        this.errorStatus = true
                        this.$toast.error(this.errorMessage);
                    }else if(aksessuar.response && aksessuar.response.status == 409){
                        if(aksessuar.response.data.message){
                            this.errorMessage = aksessuar.response.data.message;
                        }
                        this.$toast.error(this.errorMessage);
                    }else{
                        let toastTitle = 'Muvaffaqiyatli qo`shildi!'
                        this.$toast.success(toastTitle);
                        this.closeDialog();
                        await this.refresh();
                    }
                    // ============= ADDING_NEW_IP END =============


                } else {
                    // ============= IF NO SUCCESS =============
                    this.$toast.error('Xato yuz berdi!');
                }
            })

        }
        // ============= ON_SUBMIT3 END =============

    },


}
</script>


<style>

</style>

