<template>
    <div>
        <v-dialog
            max-width="400"
            persistent
            v-model="modalPayment"
        >
            <template v-slot:activator="{ on, attrs }">
                <div class="d-flex ">
                    <div v-bind="attrs" class="d-flex align-center"
                        v-on="on">
                        <img src="@/assets/icons/money-send-blue.svg" width="20" alt="money-send">
                        <span style="color: #2886FF;" class="ml-2 fs-17px">Pul berish</span>
                    </div>
                </div>
            </template>
            <template>
                 <v-card class="aksessuar_add">

                    <div class="aksessuar_add__title">
                        <span class="text-black">Pul berish</span>
                        <v-btn icon @click="closeDialog()">
                            <img src="@/assets/images/BackButton.svg" alt="BackButton">
                        </v-btn>
                    </div>

                    <ValidationObserver ref="formPayment" v-slot="{ handleSubmit, invalid }">
                        <form class="aksessuar_add__content" @submit.prevent="handleSubmit(onSubmit)">

                            <div class="mt-3">
                                <span class="text-black ml-1">Sana</span>
                                <v-menu
                                    v-model="dateMenu"
                                    :close-on-content-click="false"
                                    :nudge-right="40"
                                    transition="scale-transition"
                                    offset-y
                                    min-width="auto"
                                >
                                    <template v-slot:activator="{ on, attrs }">
                                        <ValidationProvider rules="required" v-slot="{ errors }">
                                            <v-text-field
                                                class="mt-2"
                                                placeholder="Tanlang"
                                                v-model="dateFormatted"
                                                readonly
                                                outlined
                                                v-bind="attrs"
                                                v-on="on"
                                                @blur="date = parseDate(dateFormatted)"
                                                :error-messages="errors"
                                            ></v-text-field>
                                        </ValidationProvider>
                                    </template>
                                    <ValidationProvider rules="required" v-slot="{ errors }">
                                    <v-date-picker
                                        color="blue lighten-1"
                                        v-model="date"
                                        @input="dateMenu = false"
                                        locale="ru-ru"
                                        first-day-of-week="1"
                                        :error-messages="errors"
                                    ></v-date-picker>
                                    </ValidationProvider>
                                </v-menu>
                            </div>

                            <div>
                                <span class="text-black ml-1">Summani kiriting</span>
                                <ValidationProvider :rules="{required: true, 
                                error: [errorStatus, errorMessage, errorPlace, 'total_price']}" 
                                v-slot="{ errors }">
                                    <v-text-field 
                                        outlined :error-messages="errors"
                                        onkeypress="return /[0-9,/.]/i.test(event.key)"
                                        @input="formatPrice(paid, 'paid')"
                                        v-model="paid" class="mt-2"
                                        placeholder="Misol: 1,240,000 so’m">
                                    </v-text-field>
                                </ValidationProvider>
                            </div>

                            <v-card-actions class="d-flex justify-space-between">
                                <v-btn
                                    text
                                    class="backBtn"
                                    elevation="0"
                                    @click="closeDialog()"
                                >Bekor qilish
                                </v-btn>
                                <v-btn
                                    class="primaryBtn"
                                    elevation="0"
                                    :loading="loading"
                                    :disabled="invalid"
                                    type="submit"
                                    @click="loader = 'loading'"
                                >Tasdiqlash
                                </v-btn>
                            </v-card-actions>

                        </form>
                    </ValidationObserver>
                </v-card>
            </template>
        </v-dialog>
    </div>
</template>

<script>
import {mapActions, mapState} from "vuex";
import { dateMixin, loaderMixin } from '@/mixins/dateLoaderMixins.js'

export default {
    props: ['item', 'type'],
    mixins: [dateMixin, loaderMixin],
    components: {

    },
    data() {
        return {
            // ============= SECONDARY_DATA =============
            modalPayment: false,
            paid: 0,
            // ============= SECONDARY_DATA =============



            // ============= PRIMARY_DATA =============
            errorStatus: false,
            errorMessage: '',
            errorPlace: ''
            // ============= PRIMARY_DATA =============
        }
    },
    async mounted() {
        await this.refresh()
    },
    watch: {
        // ============= OTHER_WATCHERS START =============
        paid(newValue, oldValue){
            if(newValue){
                this.clearErrors();
            }
        },
        // ============= OTHER_WATCHERS END =============
    },
    computed: {
        ...mapState({

        }),
    },
    methods: {
        ...mapActions(
            {
                postPaidOrderIncome: 'finance/postPaidOrderIncome'
            }
        ),

        // ============= PRICE_FORMATTER START =============
        formatPrice(value, input) {
            if(value != ''){

                let currency = value.toString().replace(/,/g, '');
                currency = parseFloat(currency)
                currency = new Intl.NumberFormat('en-US', {minimumFractionDigits: 0, maximumFractionDigits: 2}).format(currency);


                if(input == 'paid'){
                    this.paid = currency
                }
            }
        },
        // ============= PRICE_FORMATTER END =============




       // ============= REFRESH START =============
        async refresh(){

        },
        // ============= REFRESH END =============





        // ============= CLEARS_AND_CLOSES START =============
        clearInputs(){
            this.date = '';
            this.paid = 0;
            this.clearErrors();
        },
        clearErrors(){
            this.errorStatus = false;
            this.errorMessage = '';
            this.errorPlace = '';
        },
        closeDialog(){
            this.clearInputs();
            this.modalPayment = false
            this.$refs.formPayment.reset();
        },
        // ============= CLEARS_AND_CLOSES END =============




        // ============= ON_SUBMIT3 START =============
        async onSubmit(){
            this.$refs.formPayment.validate().then(async(success) => {
                if(success){
                    // ============= IF SUCCESS =============

                    // ============= MONEY_PAYMENT START =============
                    let params = {
                        created_at: this.date, 
                        paid: parseFloat(this.paid.toString().replace(/,/g, ''))
                    }
                    if(this.type == 'sewing'){
                        params.sewing_report_id = parseInt(this.item.id)
                    }else{
                        params.order_id = parseInt(this.item.id)
                    }

                    let payment = await this.postPaidOrderIncome(params)

                    if(payment.response && payment.response.status == 422){
                        if(payment.response.data.errors.paid){
                            this.errorMessage = payment.response.data.errors.paid[0];
                            this.errorPlace = 'paid'
                        }
                        this.errorStatus = true
                        this.$toast.error(this.errorMessage);
                    }else if(payment.response && payment.response.status == 409){
                        if(payment.response.data.message){
                            this.errorMessage = payment.response.data.message;
                        }
                        this.$toast.error(this.errorMessage);
                    }else{
                        this.$toast.success('Muvaffaqiyatli qo`shildi!');
                        this.closeDialog();
                        console.log(this.type)
                        switch(this.type) {
                            case 'material':
                                this.$store.commit('main/SET_UPDATE_FINANCE_DEBT_MATERIAL', true);
                                console.log('1')
                                break;
                            case 'collar':
                                this.$store.commit('main/SET_UPDATE_FINANCE_DEBT_COLLAR', true);
                                console.log('2')
                                break;
                            case 'painting':
                                this.$store.commit('main/SET_UPDATE_FINANCE_DEBT_PAINTING', true);
                                console.log('3')
                                break;
                            case 'factory':
                                this.$store.commit('main/SET_UPDATE_FINANCE_DEBT_FACTORY', true);
                                console.log('4')
                                break;
                            case 'sewing':
                                this.$store.commit('main/SET_UPDATE_FINANCE_DEBT_SEWING', true);
                                console.log('5')
                                break;
                            default: 
                                break;
                        }
                    }
                    // ============= MONEY_PAYMENT END =============
                }else{
                    // ============= IF NO SUCCESS =============
                    this.$toast.error('Xato yuz berdi!');
                }
            })

        }
        // ============= ON_SUBMIT3 END =============

    },
   

}
</script>


<style>

</style>