<template>
    <div>
        <v-row>
            <v-col cols="12">
                <v-data-table
                    :headers="headersDebts"
                    :items="items"
                    item-key="id"
                    :loading="loading"
                    disable-sort
                    hide-default-footer
                >
                    <!-- eslint-disable  -->
                    <template v-slot:item.where="{item, index}">
                        <v-menu 
                            v-model="menu[index]"
                            :close-on-content-click="false"
                            :nudge-width="220"
                            offset-x
                            offset-y>
                            <template v-slot:activator="{ on, attrs }">
                                <a class="d-flex" v-bind="attrs" v-on="on">
                                    <span style="color: #2782F6;">{{item.where}}</span>
                                </a>
                            </template>
                            <v-list class="process">
                                <div class="process-title">
                                    <span>Qo'shimcha ma'lumotlar</span>
                                </div>

                                <div class="pa-1">Buyurtma mahsulot nomi: {{item.product.name}}</div>
                                <div class="pa-1">Buyurtma mahsulot kodi: #{{item.product.code}}</div>
                                <div class="pa-1">Buyurtma soni: {{formatPrice(item.amount)}} ta</div>
                                <div class="pa-1">Buyurtma dona narxi: {{formatPrice(item.price)}} so`m</div>
                                <div class="pa-1">Buyurtma umumiy narxi: {{formatPrice(item.total_price)}} so`m</div>
                            </v-list>
                        </v-menu>
                    </template>

                    <template v-slot:item.debt="{item}">
                        <div class="d-flex justify-center">
                            <span class=" text-black ">{{ formatPrice(item.debt) }} so`m</span>
                        </div>
                    </template>

                    <template v-slot:item.given="{item}">
                        <div class="d-flex justify-center">
                            <span class="ml-2 text-black">{{ formatPrice(item.given) }} so`m</span>
                        </div>
                    </template>

                    <template v-slot:item.remained="{item}">
                        <div class="d-flex justify-center">
                            <span class=" text-black ">{{ formatPrice(item.remained) }} so`m</span>
                        </div>
                    </template>

                    <template v-slot:item.date="{item}">
                        <div class="d-flex justify-center">
                            <span class="ml-2 text-black">{{ item.date }}</span>
                        </div>
                    </template>

                    <template v-slot:item.giveMoney="{item}">
                        <div class="d-flex justify-center">
                            <ModalPayment v-if="$can('read', 'finance_qarzdorlik_give_money')" :item="item" :type="'collar'"></ModalPayment>
                        </div>
                    </template>

                    <!-- <template v-slot:item.actions="{item}">
                        <div class="d-flex align-center justify-center">
                            <div class="cursor-pointer mr-2 mt-1" v-if="$can('update', 'finance_qarzdorlik_yoqa_toquv')">
                                <img src="@/assets/icons/edit.svg" alt="edit" class="cursor-pointer">
                            </div>
                            <div class="cursor-pointer mt-1" v-if="$can('delete', 'finance_qarzdorlik_yoqa_toquv')">
                                <img src="@/assets/icons/trush-square.svg" alt="edit-2">
                            </div>
                        </div>
                    </template> -->

                    <template v-slot:no-data>
                        <div>
                            <no-data class="mt-6"></no-data>
                            <br>
                            <h2 class="ml-6 mt-1">Ma'lumotlar topilmadi!</h2>
                        </div>
                    </template>

                </v-data-table>
            </v-col>
        </v-row>
        <v-row>
            <v-col>
                <v-pagination
                    v-model="page"
                    :length="pageCount"
                    total-visible="7"
                    class="my-2"
                ></v-pagination>
            </v-col>
        </v-row>

    </div>
</template>

<script>
import { mapState, mapActions } from 'vuex';
import { priceMixin } from '@/mixins/formatter.js';
import NoData from "@/components/base/NoData.vue";
import ModalPayment from '@/components/modals/Finance/ModalPayment'
// import { loaderMixin } from '@/mixins/dateLoaderMixins.js'
// import DeleteModal from '@/components/modals/DeleteModal.vue'

export default {
    mixins: [priceMixin],
    components: {
        ModalPayment,
        NoData
        // DeleteModal,
    },
    data() {
        return {
            // ============= SECONDARY_DATA =============
            menu: [],
            page: 1,
            pageCount: 0,
            loading: false,
            modalPayment: false,
            editModal: false,
            deleteModal: false,
            headersDebts:[
                {text: 'Kimdan', value: 'where', align: 'start', class: 'table-header'},
                {text: 'Qarzdorlik', value: 'debt', align: 'center', class: 'table-header'},
                {text: 'Berildi', value: 'given', align: 'center', class: 'table-header'},
                {text: 'Qoldi', value: 'remained', align: 'center', class: 'table-header'},
                {text: 'Dastlabki sana', value: 'date', align: 'center', class: 'table-header'},
                {text: 'Pul berish', value: 'giveMoney', align: 'center', class: 'table-header'},
                // {text: 'Harakatlar', value: 'actions'},
            ],
            items: [],
            // ============= SECONDARY_DATA =============



            // ============= PRIMARY_DATA =============
            errorStatus: false,
            errorMessage: '',
            errorPlace: ''
            // ============= PRIMARY_DATA =============
        }
    },
    async mounted() {
        await this.refresh()
        if(!this.$can('read', 'finance_qarzdorlik_give_money')){
            this.headersDebts.pop();
        }
    },
    watch: {
        // ============= OTHER_WATCHERS START =============
        page: function (newVal, oldVal) {
            if (newVal) {
                this.refresh()
            }
        },
        '$store.state.main.finance_debt_collar': {
            handler: async function(value) {
                if(value){
                    await this.refresh()
                    this.$store.commit('main/SET_UPDATE_FINANCE_DEBT_COLLAR', false)
                }
            },
            deep: true,
            immediate: true
        },
        // ============= OTHER_WATCHERS END =============
    },
    computed: {
        ...mapState({
            finance_factory_debt: state => state.finance.finance_factory_debt,
            searchValue: state => state.main.searchValue,
                dateFilterValue: state => state.main.dateFilterValue
        }),
    },
    methods: {
        ...mapActions(
            {
                fetchFinanceFactoryDebt: 'finance/fetchFinanceFactoryDebt'
            }
        ),
       // ============= REFRESH START =============
        async refresh(){
            this.loading = true
            await this.fetchFinanceFactoryDebt({page: this.page, factory_type: 'collar', search: this.searchValue, date: this.dateFilterValue});

            this.page = this.finance_factory_debt.current_page;
            this.pageCount = Math.ceil(this.finance_factory_debt.total / 10);
            this.items = this.finance_factory_debt.data;
            this.items.forEach((elem, index) => {
                elem.where = elem.factory.name
                elem.debt = elem.total_price
                elem.given = elem.order_payments_sum_sum
                elem.remained = elem.total_price - elem.order_payments_sum_sum
                elem.date = this.$dayjs(elem.created_at).format('DD-MM-YYYY') 
            })
            this.loading = false
        },
        // ============= REFRESH END =============


    },
}
</script>

